body {
  font-family: 'Open Sans', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #CCE5E6;
}

button { 
	border:none; 
} 
.App {
  display: -webkit-flex;
  display: flex;
  margin: 0 auto;
}

.Game {
  margin: 0 auto;
  text-align: center;
}

.GameInfo ul {
  list-style-type: none;
}

.Move button {
  box-shadow: 2px 2px 2px #ccc;
  text-align: center;
  font-size: 16px;
  margin-bottom: 5px;
  height: 2.2em;
  transition: background-color 0.3s ease;
  width: 80%;
  text-decoration: none;
}

.Move button:hover {
  background-color: #F0F0F0;
  border: none;
}
.Move button:focus {
  outline: none;
}

.WinnerSquare {
  color: #60abd6;
}

#LatestMove {
  font-weight: bold;
  color: #6a874d;
}

.Square {
  -webkit-flex: 1 0 30%;
          flex: 1 0 30%; 
  display: inline-block;
  background-color: #fff;
  font-size: 4rem;
  font-weight: bold;
  height: 10rem;
  margin: 3px;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
  width: 10rem;
  cursor: pointer;
  box-shadow: 2px 2px 2px #ccc;
  transition: background-color 0.3s ease;
}

.Square:hover {
  background-color: #F0F0F0;
}

.Square:focus {
  outline: none;
}

.Square:disabled {
  pointer-events: none;
}

.kbd-navigation .Square:focus {
  background: grey;
}
.GameGrid {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  max-width: 500px;
}
.Status {
  font-size: 24px;
  font-weight: bold;
  margin: 20px auto;
}
.MovesOrder {
  box-shadow: 2px 2px 2px #ccc;
  background-color: #6a874d;
  color: white;
  text-align: center;
  font-size: 16px;
  margin-bottom: 5px;
  height: 2.2em;
  transition: background-color 0.3s ease;
  text-decoration: none;  
}

.Descending {
  -ms-box-orient: horizontal;
  display: -moz-flex;
  display: -webkit-flex;
  display: flex;

  -webkit-flex-direction: column; 
  flex-direction: column; 
  float: center;
}

.Ascending {
  -ms-box-orient: horizontal;
  display: -moz-flex;
  display: -webkit-flex;
  display: flex;

  -webkit-flex-direction: column-reverse; 
  flex-direction: column-reverse; 
  float: center;
}
